import './App.css';
import React, { useState } from 'react';
import { v4 as uuid } from 'uuid';

function App() {
    const [todos, setTodos] = useState([]); // [] | [{}, {}, ...]
    const [input, setInput] = useState("") // string
    
    // states
    // 
    // components

    const Todos = (props) => {
      if(props.todos) {

        return (
          <ul key={ uuid() }>
        { props.todos.map(v => (
          <li key={ uuid() }>
            <span key={ uuid() }>{ v.task }</span>
            <input key={ uuid() } type="checkbox" onClick={ (e) => { handleCheckBox(e, v) } }/>
          </li>
        )) }
        </ul>
      );
    } else {
      return null;
    }
    };

    // components
    // 
    // handlers

    const handleTodoSubmit = (e) => {
      e.preventDefault();

      // adding todo handler
      if(input !== "") {
        setTodos([...todos, { task: input, complete: false }])
        setInput("")

      }
    }
    
    const handleCheckBox = (e, v) => {
      // e.preventDefault();

      v.complete = e.target.checked;
    }

    const handleClearButton = () => {
      setTodos([]);
    }


  return (
    <>
    <div className="bg">

      <section className="todos-container">
      <Todos todos={ todos }/>
      <form onSubmit={ (e) => { handleTodoSubmit(e) } }>
        <input type="text" placeholder="Type todo..." value={ input } onChange={ (e) => { setInput(e.target.value) } }/>
        <input type="submit" value="Add todo"/>
      </form>
      <button onClick={ handleClearButton }>Clear todos</button>
      </section>
      
    </div>
    </>
  );
}

export default App;